import React, { useState, useEffect } from "react";
import axios from "axios";
import LoadingSpinner from "../LoadingSpinner";
//Datatable Modules
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';


const Main = () => {
  const [modalShow, setModalShow] = useState(false);

  const [tableData, setTableData] = useState([]);
  const [rowData, setRowData] = useState([]);
  const [tableError, setError] = useState();
  const [isLoading, setLoading] = useState(false);
  const [keyword, setKeyword] = useState();
  const { SearchBar } = Search;

  const openModal = (rowdata) => {
    console.log("rowData=>", rowdata)
    setRowData(rowdata)
    setModalShow(true)
  }
  const onClickFetchData = () => {
    setLoading(true)
    const fetchData = async () => {
      const url = "http://3.82.69.76/api/v1/get_by_id?id="+keyword;
      
      await axios.get(url)
        .then(response => {
          if (response.status === 200) {
            console.log("result=>", response.data)
            setTableData(response.data);
            console.log(tableData)
            setLoading(false)
          }
        })
        .catch(error => setError(error));
    }
    fetchData()
    console.log(tableData)
  };
  
  const onChange = (e) => {
    setKeyword(e.target.value);
  }

  const columns = [
    {
      dataField: 'id',
      text: 'id'
    }, 
    {
      dataField: 'CaseCourtName',
      text: 'CaseCourtName',
      formatter: (cellContent, row) => {
        return (
          <div
            onClick={() => openModal(row)}
          >
            {cellContent}
          </div>
        );
      },
    }, 
    {
      dataField: 'CaseTitle',
      text: 'CaseTitle'
    }, 
    {
      dataField: 'DateDecided',
      text: 'DateDecided'
    }, 
    {
      dataField: 'Origin',
      text: 'Origin'
    },
    {
      dataField: 'CasePDFLink',
      text: 'CasePDFLink'
    },
  ];

  function MyVerticallyCenteredModal(props) {
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            {rowData.CaseCourtName}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h4>{rowData.CaseTitle}</h4>
          <p>{rowData.FullText}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    );
  }

  return (
    <div className="container">
      <br />
      <div className="form-outline col-md-6 col-md-offset-3">
        <input type="text" id="search" className="form-control" onChange = {onChange} placeholder="Search" onKeyPress={event => {
          if (event.key === 'Enter') {
            onClickFetchData()
          }
        }}
      />
      </div>
      <br />
      <div>
        {/* <button type="button" className="btn btn-primary" onClick={() => onClickFetchData()}>
          Search
        </button> */}
      </div>
      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
        {isLoading ? <LoadingSpinner /> : tableError? tableError : 
          <ToolkitProvider
            keyField="id"
            data={ [tableData] }
            columns={ columns }
            search
          >
            
          {
            props => (
              <div>
                <SearchBar { ...props.searchProps } srText="Search this table" />
                <hr />
                <BootstrapTable
                  pagination={ paginationFactory() }
                  { ...props.baseProps }
                />
              </div>
            )
          }
          </ToolkitProvider>
        }
      </div>
    
  );
};

export default Main;