import React, { useEffect, useState } from "react";
import axios from 'axios';
import PaginationComponent from "./Pagination/PaginationComponent";
import LoadingSpinner from "../LoadingSpinner";

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Markup } from 'interweave';

const CLSearch = () => {

    
    const [data, setData] = useState([]);
    const [totalRecords, setTotalRecords] = useState(0);
    const [limit, setLimit] = useState(20);
    const [keyword, setKeyword] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [modalShow, setModalShow] = useState(false);
    const [item, setItem] = useState([]);
    const [scrapData,setScrapData] = useState({});
    


    // componentDidMount(){
    //     loadData(1);
    // }

    // loadData(1);
    // useEffect(()=>{
    //     loadData(1);
    // });
    const openModal = (item) => {

          loadSiteData(item,'');
        console.log("item=>", item)
        setItem(item);
        // this.setState({madalShow:true});
        // if(scrapData != null && scrapData != '')
        
        // useEffect(()=>{setModalShow(true);},[item]);
            
    }
    const loadData = (page, keyword) => {
        console.log('page>>', page)
        // this.setState({isLoading: true})
        setIsLoading(true);
        const fetchData = async () => {
            const url = "https://www.courtlistener.com/api/rest/v3/search/?page="+ page + '&q=' + keyword;
            
            await axios.get(url)
                .then(response => {
                if (response.status === 200) {
                    const data = response.data;
                    console.log('data>>>>', data);
                    console.log(data.results);
                    setData(data.results);
                    data.count ? setTotalRecords(data.count) : setTotalRecords(0);
                    setLimit(20);
                    setIsLoading(false);
                    
                }
                })
                
                // useEffect(()=>{
                    // setData(datas.results);
                    // datas.count ? setTotalRecords(datas.count) : setTotalRecords(0);
                    // setLimit(20);
                    // setIsLoading(false);
                // }, [datas]);

            }
            fetchData()
            
            // console.log(data)
          
    }


    const loadSiteData = (page, keyword) => {
        console.log('page>>', page)
        setIsLoading(true);
        const fetchData = async () => {
            const url = "http://3.82.69.76/api/v1/scrape?url="+ page;
            
            await axios.get(url)
                .then(response => {
                if (response.status === 200) {
                    const data = response.data;
                    console.log('data>>>>', data);
                    setScrapData(data);
                    // console.log(data.results);
                    
                    // data.count ? setTotalRecords(data.count) : setTotalRecords(0);
                    // setLimit(20);
                    setIsLoading(false);
                   
                }
                })
                
                // useEffect(()=>{
                    // setData(datas.results);
                    // datas.count ? setTotalRecords(datas.count) : setTotalRecords(0);
                    // setLimit(20);
                    // setIsLoading(false);
                // }, [datas]);

            }
            fetchData()
            setModalShow(true);
            // console.log(data)
          
    }

    const getPaginatedData = (page, keyword) => {
        loadData(page, keyword);
    }

    const onChange = (e) => {
        // setKeyword(e.target.value);
        // this.setState({keyword: e.target.value})
        setKeyword(e.target.value);

        console.log("keyword>>>", keyword)
    }


    const MyVerticallyCenteredModal = (props) => {
        return (
            <>
            
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                {isLoading ? <LoadingSpinner /> : 
                
                 <Modal.Body>
                  {/* <h4>{item.CaseTitle}</h4> */}
                  <h1>{scrapData.title}</h1>
                  <br></br>
                  
                  <Markup content={scrapData.text} />
 
                </Modal.Body>
                
                 
            }
            </Modal>

            </>
        );
    }
    // const { data, totalRecords, limit } = this.state;

    return (
        <div className="container">
            <MyVerticallyCenteredModal
                show={modalShow}
                onHide={() => setModalShow(false)}
            />
            
            <br />
            
            <div className="form-outline col-md-6 col-md-offset-3">
                <input type="text" id="search" className="form-control" onChange={onChange} placeholder="Search" onKeyPress={event => {
                    if (event.key === 'Enter') {
                        // onClickFetchData()
                        getPaginatedData(1, keyword)
                    }
                }}
                />
            </div>
            <br />
            <h4>{totalRecords} Opinions</h4>
            <table className="table">
                <thead>
                </thead>
                <tbody>
                    {
                        data && data.length > 0 ?
                            data.map((item, index) => (
                                <tr key={index}>
                                    <td scope="col">{item.id}</td>
                                    <td scope="col">
                                        
                                        <h4><a onClick={()=>openModal("https://www.courtlistener.com" + item.absolute_url)} target="_blank">{item.caseName}</a></h4>

                                        <p><b>Citations:</b> {item.citation}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                            <b>Docket Number:</b> {item.docketNumber}</p>
                                    </td>
                                </tr>
                            )) :
                            <h4>No Data Found!!</h4>
                    }
                </tbody>
            </table>
            {totalRecords > 20 &&
                <PaginationComponent
                    getAllData={getPaginatedData}
                    totalRecords={totalRecords}
                    itemsCountPerPage={limit} />
            }
        </div>
    );

}

export default CLSearch;