import { Routes, Route } from "react-router-dom";
import Main from '../pages/main';
import CLSearch from './clsearch';

const Routers = () => {
    return (
        <Routes>
          <Route exact path="/" element={<Main />} />
          <Route exact path="/search" element={<CLSearch />} />
        </Routes>
  );
};

export default Routers;
